@import "components/m_header";
@import "components/m_asside";
@import "components/m_footer";

@media screen and (max-width: 1200px), screen and (max-device-width: 1200px){
  body{
    .content{
      .container{
        .row{
          .col-md-3{
            .user{
              &__foto{
                img{
                  height: 235px;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px), screen and (max-device-width: 992px){
  body{
    .content{
      .container{
        .row{
          .col-sm-9{
            .item{
              &__img{
                margin: 0 0 0 20px;
              }
              &__info{
                &__actions{
                  a{
                    margin: 10px 50px 0 0;
                  }
                }
              }
            }
          }
          .col-sm-3{
            .user{
              &__foto{
                img{
                  height: 195px;
                }
              }
              &__data{
                p{
                  font-size: .75em;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767px), screen and (max-device-width: 767px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .user{
              display: flex;
              &__parametrs{
                a{
                  margin: 5px 0;
                  line-height: 35px;
                }
              }
            }
            .item{
              margin: 0 0 0 5px;
              &__img{
                display: none;
              }
              &__info{
                &__headline{
                  img{
                    display: block;
                  }
                }
                &__parametrs{
                  ul{
                    li{
                      span{
                        height: 45px;
                      }
                    }
                  }
                }
              }
            }
          }
          .desktop{
            display: none;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 500px), screen and (max-device-width: 500px){
  body{
    .content{
      .container{
        .row{
          .col-xs-12{
            .user{
              &__data{
                width: 100%;
              }
              &__parametrs{
                width: 100%;
              }
            }
          }
        }
      }
    }
  }
}